export default {
  name: 'Nav',
  props:['show'],
  data () {
    return {
		    msg: '技术',
		    show2:false,
		    show3:true,
		    messages:[ {
		      		name:"人脸识别",path:'face'
		      	},
		      	{
		      		name:"行人检测",path:'people'
		    }],
		    list:[{
		      		name:"产品",path:'prodact'
		      	},
		      	{
		      		name:"关于我们",path:'about'
		      	},{
		      		name:"加入我们",path:'joinUs'
		    }]
		    
		}
    },
     methods:{
		      	handleShow2(){
		      		this.show2=!this.show2;
		      		this.show3=!this.show3
		      	},
		      	handleHide(){
		      		//想办法传到父组件去,让父组件来改变
		      		 //this.show=!this.show; 
		      		 this.$emit("seek")
		      	}
		    } 
}



