import Vue from 'vue'
import Router from 'vue-router'
const Face =  r => require.ensure([],() => r(require('@/pages/face.vue')),'face');
const People =  r => require.ensure([],() => r(require('@/pages/people.vue')),'people');
const Prodact =  r => require.ensure([],() => r(require('@/pages/prodact.vue')),'prodact');
const About =  r => require.ensure([],() => r(require('@/pages/about.vue')),'about');
const Joinus =  r => require.ensure([],() => r(require('@/pages/joinus.vue')),'joinus');
const Index =  r => require.ensure([],() => r(require('@/pages/index.vue')),'index');
Vue.use(Router);
export default new Router({
   routes:[{
    path:'/face',component:Face
  },{
    path:'/people',component:People
  },{
    path:'/prodact',component:Prodact
  },{
    path:'/about',component:About
  },{
    path:'/joinUs',component:Joinus
  },{
    path:'/',component:Index
  }]
})



